import React from 'react';
import ReactDom from 'react-dom';
import Page from '../index';

class PageBasicDemo extends React.Component {

  render() {
    return (
      <div>
        <h3>size</h3>
        <h4>size="s"</h4>
        <Page
          totalPage={4}
          defaultPage={3}
          size="s"
        />
        <h4>size="m" default size</h4>
        <Page
          totalPage={4}
          defaultPage={4}
          size="m"
        />
        <h4>size="l"</h4>
        <Page
          totalPage={3}
          size="l"
        />
        <h3>event</h3>
        <Page
          totalPage={3}
          onChange={this.onChange}
        />
        <h3>huge number</h3>
        <Page
          size="s"
          totalPage={30}
        />
      </div>
    );
  }

  onChange(goPage, prevPage) {
    window.console.log(goPage, prevPage);
  }

}

ReactDom.render(<PageBasicDemo />, window.document.getElementById('basic'));
